import { useDispatch, useSelector } from 'react-redux'
import { setTargetDate } from '../../../reducers/calendar/calendarSettingSlice'
import Calendar from '../../Common/Calendar/Calendar'

interface CalendarSettingState {
  targetDate: string
}

interface RootState {
  calendarSetting: CalendarSettingState
}

export default function PanelLeftCalendar() {
  const dispatch = useDispatch()
  const { targetDate } = useSelector((state: RootState) => state.calendarSetting)

  const onClickDate = (date: Date) => {
    dispatch(setTargetDate(date.toString()))
  }

  return (
    <Calendar
      targetDate={targetDate}
      onClickDate={onClickDate}
    />
  )
}
