import { ReactNode, useEffect, useState } from 'react'
import { useRouter } from 'next/router'
import { useSelector } from 'react-redux'
import CreateEventModal from '../Modal/CreateEventModal'
import { HeaderBar } from './HeaderBar'
import PanelLeft from './Panel/PanelLeft'
import PanelRight from './Panel/PanelRight'

interface CalendarSettingState {
  isCreateEventModalOpen: boolean
}

interface RootState {
  calendarSetting: CalendarSettingState
}

interface GoogleAgendaProps {
  children: ReactNode
}

export default function GoogleAgenda({ children }: GoogleAgendaProps) {
  const router = useRouter()
  const { query } = router

  const { isCreateEventModalOpen } = useSelector(
    (state: RootState) => state.calendarSetting
  )

  const [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(false)

  useEffect(() => {
    const viewType = (query.viewType as string[]) || []
    if (viewType.length > 1 && viewType[1] === 'edit') {
      setIsEditModalOpen(true)
    } else {
      setIsEditModalOpen(false)
    }
  }, [query])

  return (
    <div className="relative flex flex-col w-screen max-h-screen min-h-screen">
      <HeaderBar />
      <div className="flex flex-1">
        <PanelLeft />
        <div className="relative flex-auto">
          {children}
        </div>
        <PanelRight />
      </div>
      {isCreateEventModalOpen && (
        <CreateEventModal />
      )}
      {isEditModalOpen && (
        <CreateEventModal isEdit />
      )}
    </div>
  )
}
